/*==================================================
=       DOCUMENT LEVEL FONT SIZE ADJUSTMENTS       =
==================================================*/

//*BASE SIZES*//
html {
	font-size: 16px;
	letter-spacing: 0.0625em;
}

// @include largetablet {
//   html { font-size: 17px; }
// }

@include desktop { 
  html { font-size: 18px; }
} 


//*SCALABLE MODULES*//
nav {
	font-size: $nav-size;
}
footer {
	font-size: $footer-size; 
}

//*TYPE SCALING*//
h1 { 
  font-size: $h1-size;
}
 
h2 {
	font-size: $h2-size;
}

h3 { 
	font-size: $h3-size;  
}
  


/*=============================
=       FONTS & COLORS        =
=============================*/
h1, h2, h3 {
	font-family: $headingFont;
}

body {
	font-family: $bodyFont;  
}

a {
  color: $genlink;
  text-decoration: none;
   transition: all 0.7s ease-in; 
  -webkit-transition: all 0.7s ease-in; 
}

a:hover {
  color: $genlinkhover;
}

/*==================================
=            NAVIGATION            =
==================================*/

nav ul li,
#home,
#portfolio,
#contact,
#about,
#portfolio p {
	text-align: center;
}

nav ul li a {
	color: $navtxt;
	transition: all 0.5s ease;
	text-decoration: none;
}

nav ul li a:hover {
	color: $navtxthover;
	transform: scale(1.3);
	    -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3); 
}

/*============================
=            HOME            =
============================*/ 

#home {
	h1,
	h2 {
		color: $homeheader;
		text-shadow: 1px 5px 3px rgba(0,0,0, 0.7), 8px 6px 15px rgba(255,255,255, 0.3);
	}
	
	h2 {
	font-size: 1.5rem;
	}
	.welcome {
		line-height: 1.5; 
		text-shadow: 2px 4px 3px rgba(0,0,0,0.5);
		color: $homebox;
	}
}


/*=============================
=            ABOUT            =
=============================*/

#about p,
#about h3,
#about h1 {
	line-height: 1.5; 
	color: $widgettext;
}

#about p,
#about h3 {
	line-height: 1.5; 
	color: $widgettext;
	text-shadow: 2px 4px 3px rgba(0,0,0,0.3);
}

#about h1 {
	text-shadow: 4px 6px 4px rgba(0, 0, 0, 0.5)
}

 .team-treehouse {
    h1, h2 {
      text-align: center;
    } 
    h1 {
      font-size: 2.2rem; 
    }
    h2 {
      font-weight: normal;
      font-size: 1.2rem; 
    }
 }

.legend {
  ul {
    font-size: 0.8em;
  }
 em {
    font-style: normal;
  }
  span {
    font-weight: bold;  
  } 
}

  #close {
    text-align: right;
  } 
  #close:hover {
    color: $navtxthover;
  }


/*=================================
=            PORTFOLIO            =
=================================*/

#portfolio {

	h1 {
		color: $widgettext;
		text-shadow: 4px 6px 4px rgba(0, 0, 0, 0.5);
	}
	h2 {
		color: $portfolioH2;
		@include largetablet {
			color: $portfolioH2-large;
		}
	}
	h3 {
		color: $portfolioH3;  
	}
	.thumb {
		p {
			font-size: 1em;
			font-weight: 700;
			color: $portfolioTxt;  
		}
	}
	p {
		color: $portfolioP;
		text-align: center; 
	}
	.overlay p {
		font-size: 0.9rem; 
		color: $widgettext;
	}
}


/*===============================
=            CONTACT            =
===============================*/
#contact {
	h1 {
		color: $widgettext;
		text-shadow: 4px 6px 4px rgba(0, 0, 0, 0.5)
	}
	.intro { 
		font-weight: 700;
		color: $widgettext;
		font-size: 1.2em; 
		text-shadow: 2px 4px 3px rgba(0,0,0,0.3);  
	}
}
.contactbox {
	color: $widgetTXT;
	ul {
		text-align: left;
	}
	h2 {
		color: $contactH;  
		text-shadow: $contactTextShadow;
	}
}

   .leftalign {
      @include largetablet { 
        text-align: center; 
      }
    }

  .socialcontact a,
  .githubcontact a {
      color: $contactbuttons;
      text-align: center;
      line-height: 50px;
  }

  .linkedinsocial,
  .twittersocial,
  .googlesocial,
  .githubsocial {
     text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.12);
     transition: color 0.5s ease-in;  
  }

  .linkedinsocial:hover,
  .twittersocial:hover,
  .googlesocial:hover,
  .githubsocial:hover {
     color: $black; 
     text-shadow: none;
  }

/*================================
=              FOOTER            =
================================*/
#footer {
	p {
		font-weight: 700;
	}
	a {
	  color: $genlinkhover;
	  text-decoration: none;
	   transition: all 0.7s ease-in; 
	  -webkit-transition: all 0.7s ease-in; 
	}

	a:hover {
	  color: $genlink;
	}

}



/*====================================
=            SPECIFIC USE            =
====================================*/

.header {
	h1 {
		color: $dkgray;
		letter-spacing: 0.1em;
		text-shadow: 0px 4px 3px rgba(0,0,0,0.4),
             0px 8px 13px rgba(0,0,0,0.1),
             0px 18px 23px rgba(0,0,0,0.1); 
	}
}

/*================================================
=            SKILL BUTTON TEXT COLORS            =
================================================*/

.html { color: $html; }
.css { color: $css; }
.javascript { color: $js; }
.jquery { color: $jq; }
.sass { color: $sass; }
.ajax { color: $AJAX; }
.mobile { color: $mobile; }
.svg { color: $svg; } 
.gulp { color: $gulp; } 